body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background-color: #c2b3ce;
  overflow-x: hidden!important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Hightlight-Holder
{
    margin: 5px;
}

.HightComponent
{
    background: #fff;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 20px;
    box-shadow: 0 1px 0px rgba(0, 0, 0, .05);
}

.hightlight-icon {
    width: 60px;
    height: 60px;
    display: inline-block;
    line-height: 60px;
    text-align: center;
    font-size: 30px;
    background: #eee;
    border-radius: 5px;
    float: left;
    margin-right: 10px;
    color: #fff;
}

.hightlight-info {
    font-size: 12px;
    padding-top: 2px;
}

span {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-size: 30px;
    font-weight: bold;
}

label span {
    font-size: 14px;
    display: inline-block;
    text-align: left;
    width:  130px;
    font-weight: normal;
}

label input {
    display: inline-block;
    text-align: left;
}

form {
    padding: 10px;
}

.side-div {
    background-color: #dedede;
    padding: 5px;
    padding-bottom: 15px;
    border-radius: 5px;
}

.dummy-blue {
    background-color: turquoise;
    padding: 5px;
    padding-bottom: 15px;
    border-radius: 5px;
}

.dummy-purple {
    background-color: blueviolet;
    padding: 15px;
    border-radius: 5px;
}

.dummy-white {
    background-color: #fff;
    padding: 15px;
    border-radius: 5px;
}

.input-form {
    padding-top: 5px;
    padding-left: 15px;
    padding-right: 15px;
}

.graph {
    min-height: 300px;
    max-height: 750px;

    min-width: 550px;
    max-width: 800px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.Logo-Navbar {
  height: 35px;
  width: 35px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

